body, html {
    height: 100%
}

a, a:active {
    color: #333;
    text-decoration: none;
    outline: 0
}

a, button, input[type=button], input[type=submit] {
    -webkit-tap-highlight-color: transparent
}

body {
    color: #333;
    font-family: "Microsoft YaHei", Arial, Verdana, serif;
    font-size: 14px;
    line-height: 1.4;
    -webkit-touch-callout: none
}

.wrapper {
    width: 100%;
    display: block;
    min-width: 320px
}

.main {
    margin: 0 auto
}

#wrap {
    position: relative;
    min-height: 100%;
    overflow: hidden
}

#pages {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center
}

.page {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

a {
    text-decoration: none;
    outline: 0;
    -webkit-tap-highlight-color: transparent
}

